.site-list{
  text-align: center;
}

.site-list__item{
  margin: 0 auto;
  padding: 22px 0;
  border-top: 1px solid $border_grey;

  &:last-child{
    border-bottom: 1px solid $border_grey;
  }

}

.site-list__link{
  color: $basic_black;

  &:hover{
    opacity: 0.6;
  }

  &:focus{
    opacity: 0.6;
  }

  &:active{
    opacity: 0.3;
  }
}

.site-list__link--active{
  @media (min-width: 768px) {
    border-bottom: 2px solid $basic_green;
  }
}
