.select{
  background-color: $basic_white;
  background: url(../../img/bg-select.jpg) top center no-repeat;

}

.select__wrapper{
  margin: 0 auto;
  padding: 27px 20px;
  min-width: 280px;
}

.select__title{
  margin: 0;
  margin-bottom: 25px;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 36px;
  font-style: normal;
  font-weight: 400;
  line-height: 36px;
  text-align: center;
  color: $basic_white;
}
