.page-nav{
  position: relative;
  left: -20px;
  width: 320px;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 20px;
  text-transform: uppercase;
  color: $basic_black;
}

.page-nav__list{
  margin: 0;
  padding: 0;
  list-style: none;
  display: none;
}

.page-nav__toggle{
  position: absolute;
  top: -20px;
  right: 20px;
  width: 18px;
  height: 18px;
  outline: none;
  border: none;
  background-color: rgba(0, 0, 0, 0);
  cursor: pointer;

  &::before{
    content: ' ';
    position: absolute;
    top: 0;
    right: -5px;
    width: 24px;
    height: 1px;
    background-color: $basic_black;
    transform: rotate(45deg);
  }

  &::after{
    content: ' ';
    position: absolute;
    top: 0;
    right: -5px;
    width: 24px;
    height: 1px;
    background-color: $basic_black;
    transform: rotate(-45deg);
  }
}
